<template>
  <div class="row">
    <div class="col-12 mt-3" v-if="isReady">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              class="no-width flat"
              circle
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">General</h5>
          </div>
          <div class="flex-align-center flex-end" v-if="ready && startEditing">
            <el-button
              @click="cancelEdit"
              type="primary"
              v-ripple.400
              class="flat"
            >
              <i class="bx bx-x mr-1"></i> Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              type="primary"
            >
              <i class="bx bx-save mr-1"></i> Save
            </el-button>
          </div>
        </div>
        <div class="col-lg-4 mb-3 mt-4">
          <p class="font-weight-bold">Store details</p>
          <p class="gray mt-3 normal-text">
            Our admin will use this store's detail to contact you.
          </p>
        </div>
        <div class="col-lg-8 mt-3 mt-lg-0 mb-3 pl-lg-0">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Details</p>
            <label for="Name" class="custom-label">Logo</label>

            <file
              @completed="setImage"
              :initialData="storeData.logo ? [{ image: storeData.logo }] : []"
            />

            <label for="Name" class="custom-label mt-3">Name</label>
            <el-input name="Name" class="w-100 mb-3" v-model="storeData.name" />
            <div class="row">
              <div class="col-6">
                <label for="Email Address" class="custom-label"
                  >Email Address</label
                >
                <el-input
                  name="Email Address"
                  class="w-100 mb-3"
                  v-model="storeData.email"
                />
              </div>
              <div class="col-6">
                <label for="Phone number" class="custom-label"
                  >Phone number</label
                >
                <el-input
                  name="Phone number"
                  class="w-100 mb-3"
                  v-model="storeData.phone"
                />
              </div>
              <div class="col-12">
                <label for="Currency" class="custom-label">Currency</label>
                <el-input
                  name="Currency"
                  class="w-100 mb-3"
                  v-model="storeData.currency"
                />
              </div>
            </div>
          </div>

          <div class="box mt-3">
            <p class="mt-0 mb-2 text-bold">Address</p>
            <div class="row">
              <div class="col-12">
                <label for="" class="custom-label md-text">State & city</label>
                <el-cascader v-model="address" :options="options"></el-cascader>
              </div>
              <div class="col-12 mt-3">
                <label for="Street address" class="custom-label"
                  >Street address</label
                >
                <el-input
                  class="w-100"
                  name="Street address"
                  v-model="street"
                ></el-input>
              </div>
            </div>
          </div>
        </div>

        <div class="col-4"></div>
        <div class="col-lg-8 pl-lg-0"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { url } from "../../../core/constant/data";
import address from "../../../core/constant/address";
import { file } from "../../common";

export default {
  data() {
    return {
      url: url,
      filteredCities: [],
      address: [],
      street: "",
      options: address,
      types: [
        "Beauty",
        "Clothing",
        "Electronics",
        "Furniture",
        "Handcrafts",
        "Jewelry",
        "Painting",
        "Photography",
        "Restaurants",
        "Groceries",
        "Other food & drink",
        "Sports",
        "Toys",
        "Services",
        "Virtual services",
        "Other",
        "I havn't decided yet",
      ],
      storeData: {
        logo: null,
        address: {
          state: "",
          city: "",
          street_address: "",
          map: "",
        },
        name: "",
        type: "",
        email: "",
        settings: {
          tax_type: "",
        },
      },
      saving: false,
      loading: null,
      startEditing: false,
      storeBeforeEdit: "",
      gettingLocation: false,
      locationBeforeEdit: "",
      locationsData: [],
      ready: false,
    };
  },
  watch: {
    storeData: {
      handler: function () {
        this.startEditing = true;
        this.startEditingGeneral = true;
      },
      deep: true,
    },
    street: {
      handler: function () {
        this.startEditing = true;
        this.startEditingGeneral = true;
      },
      deep: true,
    },
    address: {
      handler: function () {
        this.startEditing = true;
        this.startEditingGeneral = true;
      },
      deep: true,
    },
    store(value) {
      if (value) {
        this.setData();
      }
    },
  },
  mounted() {
    this.$store.commit("setIsReady", false);
    if (this.store) {
      this.setData();
    }
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    setImage($event) {
      if ($event.length > 0) {
        this.storeData.logo = $event[0].image;
      } else {
        this.storeData.logo = "/media/default.png";
      }
    },
    setData() {
      this.storeBeforeEdit = JSON.stringify(this.store);
      this.storeData = this.store;
      this.street = this.storeData.address.split(",")[0];
      this.address = [
        this.storeData.address.split(", ")[1],
        this.storeData.address.split(", ")[2],
      ];

      setTimeout(() => {
        this.ready = true;
        this.startEditing = false;
        this.$store.commit("setIsReady", true);
      }, 10);
    },
    setImages(data) {
      this.storeData.logo = data[0].image;
    },
    cancelEdit() {
      this.storeData = JSON.parse(this.storeBeforeEdit);

      setTimeout(() => {
        this.startEditing = false;
      }, 500);
    },
    Post() {
      this.saving = true;
      this.storeData.address =
        this.street + ", " + this.address[0] + ", " + this.address[1];
      this.$axios
        .put("store/", { data: this.storeData })
        .then((response) => {
          this.storeBeforeEdit = JSON.stringify(response.data);
          this.storeData = response.data;
          document.title = "OS Assist | " + response.data.name;
          this.$store.commit("setStore", response.data);
          setTimeout(() => {
            this.startEditingGeneral = false;
            this.startEditing = false;
          });
          this.saving = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: "Cool! Store has been successfully updated.",
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({ message: err, status: "error", color: "error" });
        });
    },
  },
  computed: {
    ...mapState(["store", "isReady"]),
    ...mapGetters(["checkIsReady"]),
  },
  components: {
    file,
  },
};
</script>

